import React, { useRef, useEffect } from 'react';

interface Particle {
  x: number;
  y: number;
  vx: number;
  vy: number;
  radius: number;
}

export const ConstellationBackground: React.FC = () => {
  const canvasRef = useRef<HTMLCanvasElement>(null);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext('2d');
    if (!ctx) return;

    let animationFrameId: number;
    let particles: Particle[] = [];

    const resizeCanvas = () => {
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;
      initParticles();
    };

    const initParticles = () => {
      particles = [];
      // More particles for clearer effect
      const numParticles = Math.floor((window.innerWidth * window.innerHeight) / 8000);
      
      for (let i = 0; i < numParticles; i++) {
        particles.push({
          x: Math.random() * canvas.width,
          y: Math.random() * canvas.height,
          vx: (Math.random() - 0.5) * 0.8,
          vy: (Math.random() - 0.5) * 0.8,
          radius: Math.random() * 2.5 + 1.5,
        });
      }
    };

    const drawLinesBetweenCloseParticles = () => {
      // Longer connection distance for more visible lines
      const connectionDistance = 150;
      
      for (let i = 0; i < particles.length; i++) {
        for (let j = i + 1; j < particles.length; j++) {
          const dx = particles[i].x - particles[j].x;
          const dy = particles[i].y - particles[j].y;
          const distance = Math.sqrt(dx * dx + dy * dy);

          if (distance < connectionDistance) {
            // More visible opacity
            const opacity = (1 - distance / connectionDistance) * 0.5;
            
            // Gradient from purple to blue
            const gradient = ctx.createLinearGradient(
              particles[i].x, particles[i].y,
              particles[j].x, particles[j].y
            );
            gradient.addColorStop(0, `rgba(139, 92, 246, ${opacity})`);
            gradient.addColorStop(0.5, `rgba(99, 102, 241, ${opacity})`);
            gradient.addColorStop(1, `rgba(59, 130, 246, ${opacity})`);
            
            ctx.strokeStyle = gradient;
            ctx.lineWidth = (1 - distance / connectionDistance) * 2.5;
            ctx.beginPath();
            ctx.moveTo(particles[i].x, particles[i].y);
            ctx.lineTo(particles[j].x, particles[j].y);
            ctx.stroke();
          }
        }
      }
    };

    const drawGlowingDot = (x: number, y: number, radius: number) => {
      // Brighter outer glow
      const outerGradient = ctx.createRadialGradient(x, y, 0, x, y, radius * 5);
      outerGradient.addColorStop(0, 'rgba(167, 139, 250, 0.8)');
      outerGradient.addColorStop(0.3, 'rgba(139, 92, 246, 0.4)');
      outerGradient.addColorStop(1, 'transparent');

      ctx.fillStyle = outerGradient;
      ctx.beginPath();
      ctx.arc(x, y, radius * 5, 0, Math.PI * 2);
      ctx.fill();

      // Brighter inner core
      const innerGradient = ctx.createRadialGradient(x, y, 0, x, y, radius * 2);
      innerGradient.addColorStop(0, 'rgba(255, 255, 255, 1)');
      innerGradient.addColorStop(0.5, 'rgba(199, 210, 254, 0.9)');
      innerGradient.addColorStop(1, 'transparent');

      ctx.fillStyle = innerGradient;
      ctx.beginPath();
      ctx.arc(x, y, radius * 2, 0, Math.PI * 2);
      ctx.fill();
    };

    const animate = () => {
      // Clear canvas completely for sharper effect
      ctx.fillStyle = 'rgba(17, 24, 39, 0.15)';
      ctx.fillRect(0, 0, canvas.width, canvas.height);

      // Update particle positions with boundary wrapping
      for (const particle of particles) {
        particle.x += particle.vx;
        particle.y += particle.vy;

        // Wrap around edges smoothly
        if (particle.x > canvas.width) particle.x = 0;
        else if (particle.x < 0) particle.x = canvas.width;
        
        if (particle.y > canvas.height) particle.y = 0;
        else if (particle.y < 0) particle.y = canvas.height;
      }

      drawLinesBetweenCloseParticles();

      // Draw individual glowing dots after all lines are drawn
      for (const p of particles) {
        drawGlowingDot(p.x, p.y, p.radius);
      }

      animationFrameId = requestAnimationFrame(animate);
    };

    resizeCanvas();
    initParticles();
    animate();

    const handleResize = () => {
      resizeCanvas();
    };

    window.addEventListener('resize', handleResize);

    return () => {
      cancelAnimationFrame(animationFrameId);
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  return (
    <canvas
      ref={canvasRef}
      style={{
        position: 'fixed',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        pointerEvents: 'none',
        zIndex: 0,
      }}
    />
  );
};

export default ConstellationBackground;
